*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Roboto, sans-serif;
}

h1 {
  font-family: Shrikhand, Roboto, sans-serif;
}

a {
  text-decoration: none;
  color: #000000;
}

.main-content {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f6f6f6;
  flex-flow: column nowrap;
  max-width: 1440px;
}
.main-content .header {
  width: 100%;
  display: flex;
  flex-flow: column nowrap;
}
.main-content .header__title {
  height: 63px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.1490196078);
  z-index: 50;
}
.main-content .header__title h1 {
  font-size: 30px;
  font-weight: 400;
  line-height: 43.74px;
  font-style: italic;
  letter-spacing: 0;
  color: #101010;
}
.main-content .header__location {
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #eaeaea;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.2509803922);
  z-index: 40;
}
.main-content .header__location p {
  font-size: 16px;
  font-weight: 500;
  line-height: 18.75px;
  letter-spacing: 0;
  color: #353535;
}
.main-content .header__location .header__icon-location {
  width: 12px;
  height: 16 px;
  margin-right: 17px;
}
.main-content main {
  width: 100%;
}
.main-content .reserver {
  width: 100%;
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-items: center;
  background-color: #f6f6f6;
  padding: 39px 30px 57px 30px;
}
.main-content .reserver__title {
  font-size: 24px;
  font-weight: 700;
  line-height: 28.13px;
  letter-spacing: 0;
  color: #000000;
  text-align: center;
  width: 275px;
  height: 68px;
}
.main-content .reserver__description {
  font-size: 18px;
  font-weight: 300;
  line-height: 21.09px;
  letter-spacing: 0;
  color: #353535;
  text-align: center;
  width: 315px;
  height: 46px;
  margin-bottom: 27px;
}
.main-content .reserver__ancre {
  width: 218px;
  height: 50px;
  border-radius: 25px;
  background: linear-gradient(193.33deg, #9356DC -11.44%, #FF79DA 123.93%);
  box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.2509803922);
  font-weight: 500;
  font-size: 16px;
  line-height: 18.75px;
  letter-spacing: 0;
  text-align: center;
  text-decoration: none;
  color: #FFFFFF;
  padding: 16px;
}
.main-content .fonctionnement {
  width: 100%;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: flex-start;
  background-color: #ffffff;
  padding: 48px 20px 68px 20px;
  gap: 26px;
}
.main-content .fonctionnement__title {
  font-size: 24px;
  font-weight: 700;
  line-height: 28.13px;
  letter-spacing: 0;
  color: #000000;
  width: 100%;
  height: 31px;
}
.main-content .fonctionnement__list {
  display: flex;
  flex-flow: column nowrap;
  gap: 24.93px;
  width: 100%;
  padding-left: 12px;
  list-style: none;
  counter-reset: item;
}
.main-content .fonctionnement__list .fonctionnement__item {
  width: 100%;
  border-radius: 20px;
  background-color: #f6f6f6;
  box-shadow: 0px 4px 15px 0px rgba(0, 0, 0, 0.1490196078);
  position: relative;
  padding: 28.59px 38px 28.12px 73.49px;
  font-size: 16px;
  font-weight: 500;
  line-height: 18.75px;
  counter-increment: item;
}
.main-content .fonctionnement__list .fonctionnement__item i {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  border-radius: 50%;
  color: #7E7E7E;
  left: 20px;
}
.main-content .fonctionnement__list .fonctionnement__item .fa-mobile-screen-button {
  width: 13.02px;
  height: 19.9px;
  font-size: 19.9px;
  left: 33.47px;
}
.main-content .fonctionnement__list .fonctionnement__item .fa-list-ul {
  width: 16px;
  height: 15.92px;
  font-size: 15.92px;
  left: 34.49px;
}
.main-content .fonctionnement__list .fonctionnement__item .fa-store {
  width: 18px;
  height: 17.91px;
  font-size: 17.91px;
  left: 35.49px;
  color: #9356DC;
}
.main-content .fonctionnement__list .fonctionnement__item::after {
  content: counter(item);
  position: absolute;
  left: 0;
  top: 50%;
  font-size: 24px;
  color: #FFFFFF;
  width: 24px;
  height: 24px;
  font-size: 14.18px;
  font-weight: 500;
  line-height: 16.62px;
  letter-spacing: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #9356DC;
  border-radius: 100%;
  transform: translate(-50%, -50%);
}
.main-content .restaurants {
  background-color: #f6f6f6;
  padding: 54px 17px;
  display: flex;
  flex-flow: column nowrap;
  gap: 20px;
}
.main-content .restaurants__title {
  font-size: 24px;
  font-weight: 700;
  line-height: 28.13px;
  letter-spacing: 0;
  color: #000000;
  width: 100%;
}
.main-content .restaurants__container {
  display: flex;
  flex-flow: column nowrap;
  gap: 18px;
}
.main-content .restaurants .restaurant-card {
  width: 100%;
  border-radius: 20px;
  background-color: #fff;
  overflow: hidden;
}
.main-content .restaurants .restaurant-card__article {
  display: flex;
  flex-flow: column nowrap;
  gap: 0;
  width: 100%;
}
.main-content .restaurants .restaurant-card__image {
  width: 100%;
  height: 200px;
  object-fit: cover;
}
.main-content .restaurants .restaurant-card__content {
  padding: 16px 25px 12px 13px;
  display: flex;
  flex-flow: row nowrap;
  gap: 10px;
  justify-content: space-between;
  align-items: center;
}
.main-content .restaurants .restaurant-card__title {
  font-size: 18px;
  font-weight: 700;
  line-height: 21.09px;
  letter-spacing: 0;
  color: #000000;
  height: 20px;
}
.main-content .restaurants .restaurant-card__location {
  font-size: 17px;
  font-weight: 300;
  line-height: 19.92px;
  letter-spacing: 0;
  color: #000000;
  height: 24px;
}

.footer {
  background-color: #353535;
  display: flex;
  flex-flow: column nowrap;
  width: 100%;
  padding: 22px 20px;
  gap: 16px;
}
.footer__logo {
  height: 27px;
  width: 139px;
  filter: invert(100%);
}
.footer__nav {
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  align-items: self-start;
  gap: 7px;
}
.footer__nav-link {
  font-size: 16px;
  font-weight: 500;
  line-height: 18.75px;
  letter-spacing: 0;
  color: #FFFFFF;
}
.footer__nav-icon {
  height: 13px;
  width: 11px;
  line-height: 13px;
  font-size: 13px;
  margin-right: 11px;
}